'use client';

import { useState } from 'react';
import Header from '../components/Header';
import UrlForm from '../components/UrlForm';
import HistorySidebar from '../components/HistorySidebar';

export default function Home() {
  const [isHistorySidebarOpen, setIsHistorySidebarOpen] = useState(false);
  const [urlInput, setUrlInput] = useState('');

  // Function to handle URL selection from history
  const handleUrlSelect = (url: string) => {
    setUrlInput(url);
  };

  return (
    <main className="min-h-screen bg-[#FFFDF7]">
      <Header />
      
      <div className="container mx-auto px-4 mt-16 md:mt-24">
        <UrlForm />
        
        {/* History Toggle Button */}
        <button
          onClick={() => setIsHistorySidebarOpen(true)}
          className="fixed right-4 top-20 px-4 py-2 bg-white border border-gray-200 rounded-lg shadow-sm hover:bg-gray-50"
          aria-label="Vis historik"
        >
          <svg
            className="w-6 h-6 text-gray-600"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
            />
          </svg>
        </button>

        {/* History Sidebar */}
        <HistorySidebar
          isOpen={isHistorySidebarOpen}
          onClose={() => setIsHistorySidebarOpen(false)}
          onSelectUrl={handleUrlSelect}
        />
      </div>
    </main>
  );
}
