'use client';

import { useState } from 'react';
import { useUrlHistory } from '../hooks/useUrlHistory';

export default function UrlForm() {
  const [url, setUrl] = useState('');
  const [error, setError] = useState('');
  const { addToHistory } = useUrlHistory();

  const validateUrl = (url: string) => {
    try {
      new URL(url);
      return true;
    } catch {
      return false;
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!validateUrl(url)) {
      setError('Indtast venligst en gyldig URL');
      return;
    }

    try {
      // Simulate URL examination (in a real app, this would be an API call)
      console.log('Examining URL:', url);
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Add to history
      addToHistory(url);
      
      // Clear input
      setUrl('');
      
      // Show success message (in a real app, you'd show actual results)
      alert('URL undersøgelse gennemført!\n\nI en rigtig implementation ville dette vise detaljerede resultater af kildeanalysen.');
      
    } catch (error) {
      setError('Der opstod en fejl under undersøgelsen af URL\'en');
      console.error('Error examining URL:', error);
    }
  };

  return (
    <div className="max-w-3xl mx-auto text-center px-4">
      <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-8">
        Undersøg din kilde
      </h1>
      
      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="relative max-w-2xl mx-auto">
          <div className="relative">
            <input
              type="url"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              className="w-full px-5 py-4 text-lg border-2 border-gray-200 rounded-lg 
                focus:border-[#3B3A16] focus:ring-2 focus:ring-[#3B3A16] focus:ring-opacity-20 
                focus:outline-none transition-all duration-200 shadow-sm
                placeholder-gray-400"
              placeholder="https://"
              required
            />
          </div>
          
          <button
            type="submit"
            className="mt-4 w-full md:w-auto px-8 py-4 bg-[#3B3A16] text-white text-lg 
              rounded-lg hover:bg-opacity-90 transition-colors duration-200 
              font-medium shadow-md hover:shadow-lg transform hover:-translate-y-0.5
              focus:outline-none focus:ring-2 focus:ring-[#3B3A16] focus:ring-opacity-50"
          >
            Undersøg
          </button>
        </div>

        {error && (
          <div 
            className="text-red-600 mt-2 animate-shake text-base font-medium" 
            role="alert"
          >
            <i className="fas fa-exclamation-circle mr-2"></i>
            {error}
          </div>
        )}
      </form>
    </div>
  );
}
