'use client';

import { useUrlHistory } from '@/hooks/useUrlHistory';

interface HistorySidebarProps {
  isOpen: boolean;
  onClose: () => void;
  onSelectUrl: (url: string) => void;
}

export default function HistorySidebar({ isOpen, onClose, onSelectUrl }: HistorySidebarProps) {
  const { history, clearHistory } = useUrlHistory();

  if (!isOpen) return null;

  return (
    <div className="fixed right-0 top-0 h-full w-80 bg-white shadow-xl transform transition-transform duration-300 ease-in-out z-40">
      <div className="flex flex-col h-full">
        <div className="p-6 border-b border-gray-100">
          <div className="flex justify-between items-center">
            <h2 className="text-xl font-semibold text-gray-800 flex items-center gap-2">
              <i className="fas fa-history text-[#3B3A16]"></i>
              Historik
            </h2>
            <button
              onClick={onClose}
              className="text-gray-500 hover:text-gray-700 transition-colors p-1 hover:bg-gray-100 rounded-full"
              aria-label="Luk"
            >
              <i className="fas fa-times text-xl"></i>
            </button>
          </div>
        </div>
        
        <div className="flex-1 overflow-hidden">
          <div className="h-full p-6 space-y-3 overflow-y-auto custom-scrollbar">
            {history.map((entry, index) => (
              <div
                key={index}
                onClick={() => {
                  onSelectUrl(entry.url);
                  onClose();
                }}
                className="p-4 bg-gray-50 rounded-xl hover:bg-gray-100 cursor-pointer 
                  transition-all duration-200 hover:shadow-md transform hover:-translate-y-0.5"
              >
                <div className="text-sm font-medium text-gray-800 truncate flex items-start gap-2">
                  <i className="fas fa-link text-[#3B3A16] mt-1"></i>
                  <span>{entry.url}</span>
                </div>
                <div className="text-xs text-gray-500 mt-2 flex items-center gap-1 pl-6">
                  <i className="fas fa-clock"></i>
                  {new Date(entry.timestamp).toLocaleString('da-DK')}
                </div>
              </div>
            ))}
            
            {history.length === 0 && (
              <div className="text-gray-500 text-center py-8 flex flex-col items-center gap-3">
                <i className="fas fa-history text-4xl text-gray-300"></i>
                <p>Ingen historik endnu</p>
                <p className="text-sm text-gray-400">
                  Undersøgte URLs vil blive vist her
                </p>
              </div>
            )}
          </div>
        </div>

        {history.length > 0 && (
          <div className="p-6 border-t border-gray-100">
            <button
              onClick={clearHistory}
              className="w-full px-4 py-3 text-red-600 border-2 border-red-600 rounded-lg 
                hover:bg-red-50 transition-colors duration-200 flex items-center justify-center gap-2
                font-medium"
            >
              <i className="fas fa-trash-alt"></i>
              Ryd historik
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
