'use client';

import { useState } from 'react';

interface LoginModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export default function LoginModal({ isOpen, onClose }: LoginModalProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  if (!isOpen) return null;

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // Simulate login - in a real app, this would make an API call
    console.log('Login attempt with:', { email, password });
    // For demo purposes, just close the modal
    onClose();
  };

  return (
    <div 
      className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 
        transition-opacity duration-300"
      onClick={(e) => {
        if (e.target === e.currentTarget) onClose();
      }}
    >
      <div className="bg-white rounded-xl p-8 max-w-md w-full mx-4 shadow-xl transform transition-all duration-300">
        <div className="flex justify-between items-center mb-8">
          <h2 className="text-2xl font-semibold text-gray-800">Log Ind</h2>
          <button 
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700 transition-colors p-1 hover:bg-gray-100 rounded-full"
            aria-label="Luk"
          >
            <i className="fas fa-times text-xl"></i>
          </button>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label htmlFor="email" className="block text-gray-700 font-medium mb-2">
              <i className="fas fa-envelope mr-2 text-gray-500"></i>
              Email
            </label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full px-4 py-3 border-2 border-gray-200 rounded-lg 
                focus:border-[#3B3A16] focus:ring-2 focus:ring-[#3B3A16] focus:ring-opacity-20 
                focus:outline-none transition-all duration-200"
              required
              autoComplete="email"
              placeholder="din@email.dk"
            />
          </div>
          <div>
            <label htmlFor="password" className="block text-gray-700 font-medium mb-2">
              <i className="fas fa-lock mr-2 text-gray-500"></i>
              Adgangskode
            </label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-3 border-2 border-gray-200 rounded-lg 
                focus:border-[#3B3A16] focus:ring-2 focus:ring-[#3B3A16] focus:ring-opacity-20 
                focus:outline-none transition-all duration-200"
              required
              autoComplete="current-password"
              placeholder="••••••••"
            />
          </div>
          <button
            type="submit"
            className="w-full px-6 py-3 bg-[#3B3A16] text-white rounded-lg 
              hover:bg-opacity-90 transition-all duration-200 
              font-medium shadow-md hover:shadow-lg transform hover:-translate-y-0.5
              focus:outline-none focus:ring-2 focus:ring-[#3B3A16] focus:ring-opacity-50
              flex items-center justify-center gap-2"
          >
            <i className="fas fa-sign-in-alt"></i>
            Log Ind
          </button>
        </form>
      </div>
    </div>
  );
}
